<template>
    <v-container class="pa-0">
        <!-- Header -->
        <PDHeader></PDHeader>

        <!-- Main -->
        <v-main class="py-5 main-container">
            <!-- 月份顯示 -->
            <v-container>
                <v-row justify="center">
                    <v-col cols="12" class="d-flex justify-center">

                        <div class="month-label">
                            <v-btn icon="mdi-chevron-left" class="month-btn mx-5" @click="minusMonth"></v-btn>
                            <span>{{ year }}/{{ month }}月</span>
                            <v-btn icon="mdi-chevron-right" class="month-btn mx-5" @click="plusMonth"></v-btn>
                        </div>

                    </v-col>

                </v-row>
            </v-container>

            <!-- 紀錄列表(每日) -->
            <v-card v-for="item in store_list" :key="item.date" @click="showdialog(item)"
                style="margin-bottom:5px; border:0.5px gray solid;margin:0 10px 10px 10px;">
                <v-card-title class="d-flex justify-space-between">
                    <span>{{ item.date.getMonth() + 1 }}/{{ item.date.getDate() }}</span>
                    <span>體重:{{ item.weight }}</span>
                    <span>血壓:{{ item.blood_pressure_h }}/{{ item.blood_pressure_l }}</span>
                </v-card-title>
                <v-card-text class="d-flex justify-space-between">
                    <span>脫水量:{{ item.total_dehydration }}</span>
                    <span class="d-flex justify-space-between">血糖:<span class="mx-1">早:{{ item.m_glucose }}</span><span
                            class="mx-1">中:{{ item.a_glucose }}</span><span class="mx-1">晚:{{ item.n_glucose
                            }}</span></span>
                </v-card-text>
            </v-card>

            <!-- 點擊紀錄跳出燈箱 -->
            <v-dialog v-model="show" fullscreen>
                <!-- CAPD類紀錄 -->
                <v-card v-if="type === 'CAPD'">
                    <v-card-title>{{ selectedData.date.getFullYear()
                    }}-{{ selectedData.date.getMonth() + 1 }}-{{ selectedData.date.getDate() }}</v-card-title>
                    <v-card-text class="pa-0">
                        <v-container>


                            <v-row>
                                <v-col cols="12" class="d-flex align-center">
                                    <span style="width: 30%;">血壓&nbsp;:</span>
                                    <v-row class="d-flex align-center">
                                        <v-col cols="5">
                                            <v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_h" />
                                        </v-col>
                                        <v-col cols="1"><span>/</span></v-col>
                                        <v-col cols="5">
                                            <v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_l" />
                                        </v-col>
                                    </v-row>
                                </v-col>

                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>體重&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.weight" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span style="width: 30%;">全日脫水量:&nbsp;</span><v-text-field type="number"
                                        variant="outlined" readonly color="blue" v-model="selectedData.total_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col cols="12">透析液:注入量/排出量</v-col>


                                <template v-for="(item, index) in [1, 2, 3, 4, 5]" :key="index">

                                    <v-col cols="12" class="d-flex justify-space-between align-center pb-0">
                                        <span style="width: 30%;">第{{ item }}次:&nbsp;</span>
                                        <v-text-field type="number" variant="underlined" color="blue"
                                            v-model="selectedData[`injection${item}`]" /><span>&nbsp;/&nbsp;</span>
                                        <v-text-field type="number" variant="underlined" color="blue"
                                            v-model="selectedData[`dehydration${item}`]" />
                                    </v-col>
                                    <v-col class="pt-0">
                                        <v-select :items="array" label="濃度"></v-select>
                                        <v-select :items="array" label="引流時間"></v-select>
                                        <v-select :items="array" label="排出藥水狀態"></v-select>
                                    </v-col>

                                </template>

                            </v-row>


                            <v-row>
                                <v-col cols="12">血糖濃度</v-col>
                                <v-col cols="12"> <span>早:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.m_glucose" /></v-col>
                                <v-col cols="12"> <span>中:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.a_glucose" /></v-col>
                                <v-col cols="12"> <span>晚:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.n_glucose" /></v-col>
                            </v-row>

                            <v-btn icon="mdi-close-thick" class="close-btn" @click="show = false"></v-btn>
                        </v-container>
                    </v-card-text>
                </v-card>
                <!-- APD類紀錄 -->
                <v-card v-else-if="type === 'APD'">
                    <v-card-title>{{ selectedData.date.getFullYear() }}-{{ selectedData.date.getMonth() + 1 }}-{{
                        selectedData.date.getDate() }}</v-card-title>
                    <v-card-text class="pa-0">
                        <v-container>

                            <v-row>
                                <v-col cols="12" class="d-flex align-center">
                                    <span style="width: 30%;">血壓&nbsp;:</span>
                                    <v-row class="d-flex align-center">
                                        <v-col cols="5"><v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_h" /></v-col>
                                        <v-col cols="1"><span>/</span></v-col>
                                        <v-col cols="5"><v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_l" /></v-col>
                                    </v-row>

                                </v-col>

                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>體重&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.weight" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col><span style="width: 30%;">0週期:引流量/脫水量&nbsp;</span></v-col>
                                <v-col cols="12" class="d-flex justify-space-between align-center">
                                    <v-text-field type="number" variant="underlined" color="blue"
                                        v-model="selectedData.zero_injection" /><span>&nbsp;/&nbsp;</span>
                                    <v-text-field type="number" variant="underlined" color="blue"
                                        v-model="selectedData.zero_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col><span style="width: 30%;">最末袋:引流量/脫水量&nbsp;</span></v-col>
                                <v-col cols="12" class="d-flex justify-space-between align-center">
                                    <v-text-field type="number" variant="underlined" color="blue"
                                        v-model="selectedData.last_injection" /><span>&nbsp;/&nbsp;</span>
                                    <v-text-field type="number" variant="underlined" color="blue"
                                        v-model="selectedData.last_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>APD顯示總脫水量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.APD_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row class="d-flex align-center">
                                <v-col>
                                    <span>CAPD脫水量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.CAPD_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row class="d-flex align-center">
                                <v-col>
                                    <span>全天總脫水量&nbsp;:</span><v-text-field type="number" variant="outlined" readonly
                                        color="blue" v-model="selectedData.total_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <v-select :items="array" label="平均留置時間"></v-select>
                                    <v-select :items="array" label="排出藥水狀態"></v-select>
                                    <v-select :items="array" label="機器警示次數"></v-select>

                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col cols="12">
                                    <v-textarea label="備註"></v-textarea>
                                </v-col>
                            </v-row>


                            <v-btn icon="mdi-close-thick" class="close-btn" @click="show = false"></v-btn>
                        </v-container>
                    </v-card-text>


                </v-card>

                <v-btn icon="mdi-trash-can" class="delete-btn" @click="deletedata"></v-btn>
            </v-dialog>

            <!-- 增加藥水按鈕 -->
            <router-link to="/addperitonealdialysis"><v-btn rounded="circle" icon="mdi-plus-thick"
                    class="add-btn"></v-btn></router-link>

        </v-main>

        <!-- Footer -->
        <PDFooter></PDFooter>
    </v-container>
</template>

<script setup>
// components
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue'
import { ref } from 'vue';
import { useStore } from 'vuex';


const show = ref(false);
// 假資料
const selectedData = ref({});
const dataindex = ref();
const type = ref('');
const store_list = useStore().state.list;
const array = ref([1, 2, 3, 4])
const year = ref(new Date().getFullYear())
const month = ref(new Date().getMonth() + 1)

// 月份增減
const plusMonth = () => {
    if (month.value === 12) {
        month.value = 1
        year.value += 1;
    } else {
        month.value++
    }
}

const minusMonth = () => {
    if (month.value === 1) {
        month.value = 12
        year.value -= 1;
    } else {
        month.value--
    }
}

// 燈箱
const showdialog = (item) => {
    show.value = true;
    selectedData.value = item;
    dataindex.value = item.num - 1
    type.value = item.type;
};

// 刪除資料
const deletedata = () => {
    console.log(dataindex.value);
    store_list.splice(dataindex.value, 1);
    show.value = false;
}






</script>

<style lang="scss" scoped>
.main-container {
    position: relative;

    a {
        text-decoration: none;
        position: fixed;
        right: 10px;
        bottom: 70px;

        .add-btn {
            width: 60px;
            height: 60px;
            padding: 0;
            font-size: 24px;
            background-color: rgb(22, 231, 3);
            color: black;
        }



    }

    .month-label {
        font-size: 24px;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .month-btn {
            font-size: 16px;
            background-color: lightgray;
        }
    }

}

.close-btn {
    position: fixed;
    box-shadow: none;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    font-size: 20px;
}

.delete-btn {
    position: fixed;
    bottom: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    font-size: 20px;
    background-color: red;
}
</style>